@import '../../assets/stylesheets/application.scss';

.navigation {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 400;

    &__checkbox {
        display: none;
    }

    &__button {
        background-color: $color-offwhite;
        height: 5rem;
        width: 5rem;
        position: fixed;
        top: 2rem;
        right: 2rem;
        border-radius: 50%;
        z-index: 2000;
        box-shadow: 0 1rem 3rem rgba($color-offblack, .1);
        text-align: center;
        cursor: pointer;

        // @include respond(tab-port) {
        //     top: 4rem;
        //     right: 4rem;
        // }

        // @include respond(phone) {
        //     top: 3rem;
        //     right: 3rem;
        // }
    }

    &__background {
        height: 4rem;
        width: 4rem;
        border-radius: 50%;
        position: fixed;
        top: 2.5rem;
        right: 2.5rem;
        background-image: radial-gradient($color-lover-blue, $color-lover-pink);
        z-index: 1000;
        transition: transform .8s cubic-bezier(0.86, 0, 0.07, 1);

        // @include respond(tab-port) {
        //     top: 4.5rem;
        //     right: 4.5rem;
        // }

        // @include respond(phone) {
        //     top: 3.5rem;
        //     right: 3.5rem;
        // }
    }

    &__nav {
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1500;

        opacity: 0;
        width: 0;
        transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }

    &__list {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        list-style: none;
        text-align: center;
        width: 100%;
    }

    &__item {
        margin: 1rem;
    }

    &__link {
        display: inline-block;
        font-size: 3rem;
        font-weight: 300;
        padding: 1rem 2rem;
        color: $color-offwhite;
        text-decoration: none;
        text-transform: uppercase;
        background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-offwhite 50%);
        background-size: 220%;
        transition: all .4s;
        cursor: pointer;

        span {
            margin-right: 1.5rem;
            display: inline-block;
        }
        
        &:hover,
        &:active {
            background-position: 100%;
            color: $color-lover-pink;
            transform: translateX(1rem);
        }
    }


    //FUNCTIONALITY
    &__checkbox:checked ~ &__background {
        transform: scale(80);
    }

    &__checkbox:checked ~ &__nav {
        opacity: 1;
        width: 100%;
    }


    //ICON
    &__icon {
        position: relative;
        margin-top: 2.5rem;

        &,
        &::before,
        &::after {
            width: 3rem;
            height: 2px;
            background-color: $color-offblack;
            display: inline-block;
        }

        &::before,
        &::after {
            content: "";
            position: absolute;
            left: 0;
            transition: all .2s;
        }

        &::before { top: -.8rem; }
        &::after { top: .8rem; }
    }

    &__button:hover &__icon::before {
        top: -1rem;
    }

    &__button:hover &__icon::after {
        top: 1rem;
    }

    &__checkbox:checked + &__button &__icon {
        background-color: transparent;
    }

    &__checkbox:checked + &__button &__icon::before {
        top: 0;
        transform: rotate(135deg);
    }

    &__checkbox:checked + &__button &__icon::after {
        top: 0;
        transform: rotate(-135deg);
    }
}